import React, { useState,useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Content,
  Header,
  Page,
  pageTheme,
  ContentHeader,
} from '@backstage/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { DefaultApi } from '../../api/apis';

import { Alert } from '@material-ui/lab';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';






const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'center',
    },
    margin: {
      margin: theme.spacing(3),
    },
    withoutLabel: {
      marginTop: theme.spacing(3),
    },
    textField: {
      width: '25ch',
    },
  }),
);


const initialUserState = {
  name: 'Makawan Thojan',
  n: 10,
  
};

export default function Create() {
  const classes = useStyles();
  const profile = { givenName: '' };
  const username = { givenuser: 'faomg201@gmail.com' };
  const logout = { givenlogout: 'logout' };
  const api = new DefaultApi();
  const [loading, setLoading] = useState(true);

  const [user, setUser] = useState(initialUserState);
  const [foodmenus, setFOODMENU] = useState([]);
  const [mainingres, setMainingre] = useState([]);
  const [sources, setSource] = useState([]);
  const [recordfoods, setRec] = useState([]);
  const [status, setStatus] = useState(false);
  const [alert, setAlert] = useState(true);

  const [foodmenuid, setFOODMENUID] = useState(Number);
  const [mainingreid, setMainingreID] = useState(Number);
  const [sourceid, setSourceID] = useState(Number);

  useEffect(() => {    
    const getFOODMENU = async () => {
      const res = await api.listFoodmenu({ limit: 10, offset: 0 });
      setLoading(false);
      setFOODMENU(res);
    };
    getFOODMENU();

    const getMainingre = async () => {
      const res = await api.listMainingre({ limit: 10, offset: 0 });
      setLoading(false);
      setMainingre(res);
    };
    getMainingre();

    const getSource = async () => {
      const res = await api.listSource({ limit: 10, offset: 0 });
      setLoading(false);
      setSource(res);
    };
    getSource();


  }, [loading]);

  const FOODMENUhandleChange = (event) => {
    setFOODMENUID(event.target.value );
  };

  const MainingrehandleChange = (event) => {
    setMainingreID(event.target.value );
  };

  const SourcehandleChange = (event) => {
    setSourceID(event.target.value );
  };

  const CreateRecordfood = async () => {
    const recordfood = {
      recordFOODMENU: foodmenuid,
      recordINGREDIENT: mainingreid,
      recordSOURCE: sourceid,
      recordUSER: 2
    };
    
    console.log(recordfood);
    const res = await api.createRecordfood({ recordfood: recordfood });
    setStatus(true);
    if (res.id != '') {
      setAlert(true);
    } else {
      setAlert(false);
    }
    const timer = setTimeout(() => {
      setStatus(false);
    }, 1000);
  };

  return (
    React.createElement(Page, { theme: pageTheme.home,}
      , React.createElement(Header, {
        title: ` ${profile.givenName || 'to Backstage'}`,
        subtitle: "",}
      )
      , React.createElement(Content, null
      , React.createElement(ContentHeader, {  title: "",}
      , React.createElement(Typography, { align: "left", style: { marginRight: 16, color: "#00eeff" },}
            , username.givenuser
          )
          , React.createElement(Button, { variant: "contained", color: "primary",}, ""

          )
          , status ? (
            React.createElement('div', null
              , alert ? (
                React.createElement(Alert, { severity: "success",}, ""

                )
              ) : (
                  React.createElement(Alert, { severity: "warning", style: { marginTop: 20 },}, ""

                  )
                )
            )
          ) : null
        )

        , React.createElement('div', { className: classes.root,}
          , React.createElement('form', { noValidate: true, autoComplete: "off",}
          , React.createElement(FormControl, {
                className: classes.margin,
                variant: "outlined",}
              
                , React.createElement(InputLabel, { id: "foodmenu-label",}, "")
                , React.createElement(Select, {
                  labelId: "foodmenu-label",
                  id: "foodmenu",
                  value: foodmenuid,
                  onChange: FOODMENUhandleChange,
                  style: { width: 400 },}
                
                , foodmenus.map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.fOODMENUNAME)
                ))
                )
              )

            , React.createElement('div', null
              , React.createElement(FormControl, {
                className: classes.margin,
                variant: "outlined",}
              
                , React.createElement(InputLabel, { id: "Mainingre-label",}, "")
                , React.createElement(Select, {
                  labelId: "Mainingre-label",
                  id: "mainingre",
                  value: mainingreid,
                  onChange: MainingrehandleChange,
                  style: { width: 400 },}
                
                , mainingres.map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.mAININGREDIENTNAME)
                ))
                )
              )
            )

            , React.createElement(FormControl, {
              className: classes.margin,
              variant: "outlined",}
            
              , React.createElement(InputLabel, { id: "Source-label",}, "")
              , React.createElement(Select, {
                labelId: "Source-label",
                id: "source",
                value: sourceid,
                onChange: SourcehandleChange,
                style: { width: 200 },}
              
                , sources.map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.sOURCENAME)
                ))
              )
            )
            , React.createElement('div', { className: classes.margin,}
              , React.createElement(Button, {
                onClick: () => {
                  CreateRecordfood();
                },
                variant: "contained",
                color: "primary",}
              , ""

             )
              , React.createElement(Button, {
                style: { marginLeft: 20 },
                component: RouterLink,
                to: "/",
                variant: "contained",}
              , ""

             )
            )
          )
        )
      )
    )
  );
}
